<template>
  <div >
    <cms-breadcrumb to="/coupon/list" type='list' @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>可进行修改商家优惠券</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px" v-loading='loading' >
        <el-form-item label="优惠券名称" prop="coupon.name" :error='errorMessage["coupon_name"]'>
          <el-input v-model="formData.coupon.name" maxlength="30"></el-input>
        </el-form-item>
      <el-form-item label="有效期" prop="coupon.useBeginTime">
          <cms-picker disabledType='before' v-model="activeTimer" style="width:380px" @change="getActiveTimer"></cms-picker>
        </el-form-item>
        <el-form-item label="使用条件" prop="coupon.conOrderAmount" :error='errorMessage["coupon_conOrderAmount"]'>
          <el-input v-model="formData.coupon.conOrderAmount" maxlength="6">
            <template slot="prepend">满</template>
            <template slot="append">元</template>
          </el-input>
        </el-form-item>
        <el-form-item label="面值" prop="coupon.discountAmount" :error='errorMessage["coupon_discountAmount"]'>
          <el-input v-model="formData.coupon.discountAmount" maxlength="6">
            <template slot="append">元</template>
          </el-input>
        </el-form-item>
        <el-form-item label="发放限制" prop="coupon.issueNum">
          <el-input v-model="formData.coupon.issueNum" maxlength="6"></el-input>
          <div class="gray">输入几表示限制几张，输入0表示不限制</div>
        </el-form-item>
        <el-form-item label="领取限制" prop="coupon.getNum" :error='errorMessage["coupon_getNum"]'>
          <el-input v-model="formData.coupon.getNum" maxlength="2">
            <template slot="prepend">每用户</template>
            <template slot="append">张</template>
          </el-input>
        </el-form-item>
       
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update()" v-color>保存</cms-button>
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { couponUpdate, couponGet } from '@/api/marketing/index.js'
import categoryCascader from '@/components/cms/categoryCascader'
var defaultData = {
  coupon: {
    conOrderAmount: '',
    discountAmount: '',
    getMode: '',
    getNum: '',
    expirateType: '',
    issueNum: '',
    name: '',
    useBeginTime: '',
    useEndTime: '',
    useDay: '',
    couponType: '',
    couponScope: ''
  },
  ctgIds: [],
  activeTimer: []
}
export default {
  components: { categoryCascader },
  mixins: [form],
  data() {
    var isAmount = (rule, value, callback) => {
      if (value < this.formData.coupon.conOrderAmount) {
        callback()
      } else {
        callback(new Error('面值不能大于使用条件'))
      }
    }
    return {
      formData: Object.assign({}, defaultData),
      rules: {
        'coupon.name': [rules.required('请填写优惠券名称'), rules.len(1, 20)],
        'coupon.couponType': [rules.required('')],
        'coupon.useDay': [rules.number('请输入大于或等于0的正整数')],
        'coupon.conOrderAmount': [rules.required(), rules.number('请输入大于或等于0的正整数')],
        'coupon.issueNum': [rules.required(), rules.number('请输入大于或等于0的正整数')],
        'coupon.getNum': [rules.required(), rules.number('请输入大于或等于0的正整数')],
        'coupon.useBeginTime': [rules.required()],
        'coupon.discountAmount': [
          rules.required(),
          rules.number('请输入大于或等于0的正整数'),
          { validator: isAmount, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    getActiveTimer(value) {
      this.formData.coupon.useBeginTime = value[0]
      this.formData.coupon.useEndTime = value[1]
    },
    getFormData(id) {
      this.status = false
      this.loading = true
      couponGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData.coupon = Object.assign({}, res.data)
          if (res.data.useBeginTime && res.data.useBeginTime !== '') {
            this.activeTimer = [this.$moment(res.data.useBeginTime), this.$moment(res.data.useEndTime)]
          }
          this.formData.issueType = res.data.issueNum === 0 ? 1 : 2
          delete this.formData.coupon.ctgs
          this.$set(this.formData, 'ctgIds', res.data.ctgs.map(v => v.id))
          this.status = true
          this.loading = false
        }
      })
    },
    changeType(value) {
      this.formData.coupon.expirateType = value
    },
    getTimer(value) {
      this.formData.coupon.useBeginTime = value[0]
      this.formData.coupon.useEndTime = value[1]
    },
    update() {
      this.updateForm(couponUpdate, this.formData, '/coupon/list', 'list')
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>

<style>
</style>



